@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap);
body {
  margin: 0;
  font-family: 'Roboto', Arial, Helvetica, sans-serif;
}

hr {
  background-color: #00897b;
}

.hor-padding {
  padding: 0 20px;
}

@-webkit-keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.page__loading-spinner {
  width: 20vw;
  height: 20vw;
  position: absolute;
  left: 40vw;
  top: 30vh;
  border: solid 40px #00897b;
  border-radius: 50%;
  border-top-color: transparent;
  border-bottom-color: transparent;
  -webkit-animation: spin 1s linear infinite;
          animation: spin 1s linear infinite;
}

.header {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  background-color: #b2dfdb;
}

.page__content {
  max-width: 1000px;
  margin: 0 auto;
  padding: 0 15px;
}

.page__wrapper {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

.page__news {
  min-width: 300px;
}

.page__news-wrapper {
  -webkit-flex-grow: 1;
          flex-grow: 1;
	width: 50%;
}

.form {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  width: 300px;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  margin-top: 70px;
  margin-left: 15px;
  padding-top: 20px;
  padding-bottom: 20px;
  border: solid 2px #00897b;
}

.form__input {
  margin-right: 30px;
  margin-bottom: 20px;
}

.form__btn {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  width: 150px;
  min-height: 40px;
  background-color: #b2dfdb;
  border: solid 2px #00897b;
  border-radius: 3px;
  outline: none;
  cursor: pointer;
}

.form__btn--del-item {
  width: 100px;
  min-height: 20px;
  background-color: #ffcdd2;
  border: solid 2px #d32f2f;
}

.form__btn:hover {
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}

.form__btn:active {
  -webkit-transform: scale(0.95);
          transform: scale(0.95);
}
